.footer {
	background-color: #191919;
	padding: 70px 0 0;
}
@media (max-width: 767px) {
	.footer {
		text-align: center;
	}
}
.footer .container {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
	gap: 40px;
}

.footer .box h3 {
	font-size: 50px;
	color: white;
	margin: 0 0 20px;
}
.footer .box .social {
	display: flex;
}
@media (max-width: 767px) {
	.footer .box .social {
		justify-content: center;
	}
}
.footer .box .social li {
	margin-right: 10px;
}
.footer .box .social li a {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	background-color: #313131;
	color: #b9b9b9;
	width: 50px;
	height: 50px;
	font-size: 20px;
	transition: var(--main-transition);
}
.footer .box .social .facebook:hover {
	background-color: #1877f2;
}
.footer .box .social .twitter:hover {
	background-color: #1da1f2;
}
.footer .box .social .youtube:hover {
	background-color: #ff0000;
}
.footer .box .text {
	line-height: 1.5;
	color: #b9b9b9;
}
.footer .box .links li {
	padding: 15px 0;
	transition: var(--main-transition);
}
.footer .box .links li:not(:last-child) {
	border-bottom: 1px solid #444;
}
.footer .box .links li:hover {
	padding-left: 10px;
}
.footer .box .links li:hover a {
	color: white;
}
.footer .box .links li a {
	color: #b9b9b9;
	transition: var(--main-transition);
}
.footer .box .links li span {
	font-weight: 900;
	margin-right: 10px;
	color: var(--main-color);
}
.footer .box .line {
	display: flex;
	align-items: center;
	color: #b9b9b9;
	margin-bottom: 30px;
}
@media (max-width: 767px) {
	.footer .box .line {
		flex-direction: column;
	}
}
.footer .box .line i {
	color: var(--main-color);
	margin-right: 10px;
	font-size: 25px;
}
@media (max-width: 767px) {
	.footer .box .line i {
		margin-bottom: 10px;
	}
}
.footer .box .line .info {
	font-size: 12px;
	line-height: 1.7;
	flex: 1;
}
.footer .box .line .info span {
	display: block;
	margin-bottom: 5px;
}
.footer .footer-gallery img {
	width: 78px;
	border: 3px solid white;
	margin: 2px;
}
.footer .copyright {
	border-top: 1px solid #444;
	text-align: center;
	margin: 50px 0 0;
	padding: 25px 0;
	color: white;
}